// Quick CSS reset

html,
body {
  height: 100%;
  font-family: "Open Sans";
}

@media screen and (min-width: 25em) {
  html {
    font-size: calc(16px + (24 - 16) * (100vw - 400px) / (800 - 400));
  }
}

html {
  font-size: 6vw;
}

.welcome {
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  align-items: center;
  justify-content: center;
  background-image: radial-gradient(ellipse, $color-normal, $color-dark);

  .welcome__description {
    line-height: 1.5;
    padding: 0 0.5rem;
  }

  h1 {
    color: $color-white;
    // font-size: 3.8125rem;
    // font-size: 3rem;
  }

  a.scroll-down {
    color: $color-white;
    text-decoration: none;
    font-size: 1rem;
    padding: 0.3rem 0.6rem;
    border-radius: 0.3rem;
    margin: 1rem;
    border: 0.1rem solid $color-white;
    position: relative;
    background-color: $color-dark;
    padding-right: 1.3rem;
    display: flex;
    justify-content: center;
    align-items: center;

    &:hover {
      border: 0.1rem solid $color-normal;
    }


  }

  .continue-arrow {
    margin: 0 0.2rem;

    &:after {
      position: absolute;
      // right: 0.5rem;
      // top: 1.2rem;
      content: '';
      display: block;
      width: 0px;
      height: 0px;
      border-top: 0.5rem solid $color-white;
      border-left: 0.5rem solid transparent;
      border-right: 0.5rem solid transparent;
    }

  }

  //   &>* {
  //     width: 100%;
  //   }
}

